import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import 'bootstrap/dist/css/bootstrap.min.css';
import Home from './pages/Home.js'
import About from './pages/About.js'
import Portfolio from './pages/Portfolio.js'
import Experiences from './pages/Experiences.js';
import Outliner from'./pages/Outliner.js'
import ARSlider from './pages/ARSlider.js'
import ScanFx from './pages/ScanFx.js'
import UnrealForDesigners from './pages/UnrealForDesigners.js'
import Navigation from './component2/Navigation.jsx';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

ReactDOM.render(
  <Router>
    <Navigation />
    <Routes>
      <Route path="/" element={<Home />} />
      <Route path="/about" element={<About />} />
      <Route path="/portfolio" element={<Portfolio />} />
      <Route path="/experiences" element={<Experiences />}/>
      <Route path="/outliner" element={<Outliner/>}/>
      <Route path="/slider" element={<ARSlider/>}/>
      <Route path="/scan" element={<ScanFx/>}/>
      <Route path="/unrealfordesign" element={<UnrealForDesigners/>}/>
    </Routes>
  </Router>,

  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
